import { createRouter, createWebHashHistory } from 'vue-router'
// import { createRouter, createWebHistory } from 'vue-router'
import index from '@v/index'
import Home from '@v/home'
import Demo from '@v/demo'
import Menu1 from '@v/menu1'
import Menu2 from '@v/menu2'

const routes = [

  {
    path: '/',
    name: 'index',
    redirect: '/home',
    component: index,
    children: [
      {
        path: '/home',
        name: 'Home',
        component: Home
      },
      {
        path: '/demo',
        name: 'Demo',
        component: Demo
      },
      {
        path: '/menu1',
        name: 'Menu1',
        component: Menu1
      },
      {
        path: '/menu2',
        name: 'Menu2',
        component: Menu2
      },
    ]

  },


  // {
  //   path: '/about',
  //   name: 'About',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "about" */ '@v/About.vue')
  // },
  // {
  //   path: '/tpanorama',
  //   name: 'tpanorama',
  //   component: () => import(/* webpackChunkName: "about" */ '@v/tpanoramaPage/index.vue')
  // }
]

const router = createRouter({
  // history: createWebHistory(process.env.BASE_URL),
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
